import React, {Component} from 'react';
import SkillsTab from "../views/tabs/skillsTab";
import pageHead from "../../../../../../packages/ext/pageTitle/singletons/pageTitle";
import container from "../../../../../../packages/core/container/singletons/container";
import SkillsMarkList from "../views/tabs/skillsMarkList";
import modalFacade from "../../../../../../packages/bundles/modal/domain/facades/modalFacade";
import {Button} from "react-bootstrap";
import SkillServiceInterface from "../../../domain/services/SkillServiceInterface";

export default class SkillsComponent extends Component {

    private title = 'Навыки';

    private service: SkillServiceInterface;

    state = {
        collection: null,
    };

    constructor(props) {
        super(props);
        this.service = container.get('resume.services.skill');
    }

    componentDidMount() {
        let collection = this.service.findAll();
        collection.then((collection) => {
            this.setState({collection});
        });
    }

    render() {
        let collection = this.state.collection;

        if (collection === null) {
            return null;
        }

        pageHead.setPageTitle(this.title);

        const title = 'Условное обозначение уровня знаний';
        let body = (
            <div className="modal-body">
                <SkillsMarkList/>
            </div>
        );
        let showMarkListInModal = () => modalFacade.showContent(title, body);

        return (
            <>
                <Button variant="link" title={title} onClick={showMarkListInModal} className="float-right">
                    <i className="fas fa-question"/>
                </Button>
                <h3 className="fw-bold">
                    {this.title}
                </h3>
                <SkillsTab skills={collection}/>
            </>
        );
    }
}
