import React, {Component} from 'react';
import AboutTab from "../views/tabs/aboutTab";
import pageHead from "../../../../../../packages/ext/pageTitle/singletons/pageTitle";
import container from "../../../../../../packages/core/container/singletons/container";
import AboutServiceInterface from "../../../domain/services/AboutServiceInterface";

export default class AboutComponent extends Component {

    private title = 'О себе';

    private service: AboutServiceInterface;

    state = {
        entity: null,
    };

    constructor(props) {
        super(props);
        this.service = container.get('resume.services.about');
    }

    componentDidMount() {
        let entity = this.service.findOne();
        entity.then((entity) => {
            this.setState({entity});
        });
    }

    render() {
        let aboutEntity = this.state.entity;

        if (aboutEntity === null) {
            return null;
        }

        pageHead.setPageTitle(this.title);
        return (
            <>
                <h3 className="fw-bold">
                    {this.title}
                </h3>
                <AboutTab entity={aboutEntity}/>
            </>
        );
    }
}
