import SkillServiceInterface from "../../../domain/services/SkillServiceInterface";
import BaseRestApiAdapter from "./BaseRestApiAdapter";

export default class SkillRestApiAdapter extends BaseRestApiAdapter implements SkillServiceInterface {

    async findAll() {
        return new Promise((resolve, reject) => {
            let uri = this.apiEndpoint('skills');
            this.axiosInstance.get(uri)
                .then((result) => {
                    resolve(result.data);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
