import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import AboutRestApiAdapter from "../../infrastructure/restApi/adapters/AboutRestApiAdapter";
import axios from "axios";
import UrlHelper from "../../../../../packages/ext/html/helpers/UrlHelper";
import ContactRestApiAdapter from "../../infrastructure/restApi/adapters/ContactRestApiAdapter";
import HomeRestApiAdapter from "../../infrastructure/restApi/adapters/HomeRestApiAdapter";
import CompanyRestApiAdapter from "../../infrastructure/restApi/adapters/CompanyRestApiAdapter";
import ProjectRestApiAdapter from "../../infrastructure/restApi/adapters/ProjectRestApiAdapter";
import SkillRestApiAdapter from "../../infrastructure/restApi/adapters/SkillRestApiAdapter";

export default function configureDi(domainName: string) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    const axiosInstance = axios.create();
    axiosInstance.defaults.baseURL = UrlHelper.getLocation().origin;

    bundleDiConfigurator.bind("services.about", new AboutRestApiAdapter(axiosInstance));
    bundleDiConfigurator.bind("services.contact", new ContactRestApiAdapter(axiosInstance));
    bundleDiConfigurator.bind("services.home", new HomeRestApiAdapter(axiosInstance));
    bundleDiConfigurator.bind("services.company", new CompanyRestApiAdapter(axiosInstance));
    bundleDiConfigurator.bind("services.project", new ProjectRestApiAdapter(axiosInstance));
    bundleDiConfigurator.bind("services.skill", new SkillRestApiAdapter(axiosInstance));

}
