import HomeServiceInterface from "../../../domain/services/HomeServiceInterface";
import BaseRestApiAdapter from "./BaseRestApiAdapter";

export default class HomeRestApiAdapter extends BaseRestApiAdapter implements HomeServiceInterface {

    async findOne() {
        return new Promise((resolve, reject) => {
            let uri = this.apiEndpoint('home');
            this.axiosInstance.get(uri)
                .then((result) => {
                    resolve(result.data);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
