import React from 'react';
import ImageSliderWidget from "../../../../../../../packages/widgets/imageSlider/ImageSliderWidget";
import Html from "../../../../../../../packages/widgets/html/Html";
import modalFacade from "../../../../../../../packages/bundles/modal/domain/facades/modalFacade";

export default function ProjectTab(props) {
    let items = props.projects;
    return (
        <>
            {items.map(function (item, key) {
                let projectSite = (
                    <>
                        <i className="fas fa-link"/>
                        &nbsp;
                        <a href={item.site} target="_blank" rel="noreferrer">
                            {item.site}
                        </a>
                    </>
                );
                let projectCompanyPosition = (
                    <>
                        <span title="Компания">{item.company}</span> | <span
                        title="Должность">{item.position}</span>
                    </>
                );
                let projectDescription = (
                    <>
                        <Html>
                            {item.description}
                        </Html>
                    </>
                );
                let projectTechStack = (
                    <>
                        {item.techUnit.map(function (item, key) {
                            return (
                                <span key={key} className="badge badge-primary ml-1 mr-1">
                                    {item}
                                </span>
                            );
                        })}
                    </>
                );
                let body = (
                    <>
                        <div className="modal-body">

                            <div className="">
                                {item.images ? (
                                        <ImageSliderWidget
                                            items={item.images}
                                            showBullets={true}
                                            showPlayButton={false}

                                        />
                                ) : (
                                    <img src="/img/no-image-wide.png" alt="" style={{width: "100%"}}/>
                                )}
                            </div>

                            {item.site ? (
                                <p className="card-text">
                                    {projectSite}
                                </p>
                            ) : null}

                            <p className="card-text">
                                {projectCompanyPosition}
                            </p>

                            <p className="card-text">
                                {projectDescription}
                            </p>

                            <h6 className="card-subtitle mb-2">
                                Основной стек
                            </h6>

                            <p className="card-text">
                                {projectTechStack}
                            </p>
                        </div>
                    </>
                );
                let showProjectInModal = () => modalFacade.showContent(item.title, body);

                let projectImage = (
                    <>
                        {item.images ? (
                            <img
                                src={item.images[0].thumbnail}
                                alt={item.title}
                                style={{width: "300px", cursor: "pointer"}}
                                onClick={showProjectInModal}
                            />
                            /*<div
                                style={{width: '300px', maxWidth: '100%'}}>
                                <ImageSliderWidget
                                    items={item.images}
                                    showThumbnails={false}
                                    showBullets={true}
                                    showPlayButton={false}
                                />
                            </div>*/
                        ) : (
                            <img
                                src="/img/no-image-wide.png"
                                alt={item.title}
                                style={{width: "300px", cursor: "pointer"}}
                                onClick={showProjectInModal}
                            />
                        )}
                    </>
                );

                return (
                    <div key={key} className="card mt-3">
                        <div className="card-body">

                            <div className="float-left pr-3">
                                {projectImage}
                            </div>

                            <div>

                                <h5 className="card-title" onClick={showProjectInModal} style={{cursor: "pointer"}}>
                                    {item.title}
                                </h5>

                                {item.site ? (
                                    <p className="card-text">
                                        {projectSite}
                                    </p>
                                ) : null}

                                <p className="card-text">
                                    {projectCompanyPosition}
                                </p>

                                <p className="card-text">
                                    {projectDescription}
                                </p>

                                <h6 className="card-subtitle mb-2">
                                    Основной стек
                                </h6>

                                <p className="card-text">
                                    {projectTechStack}
                                </p>
                            </div>

                        </div>
                    </div>
                );
            })}

        </>
    );
};
