import React from 'react';
import ValidatorHelper from "../../../../../../../packages/ext/string/helpers/ValidatorHelper";

export default function MainContactTab(props) {
    return (
        <ul>
            {props.contacts.map(function (item, key) {
                let isMain = item.hasOwnProperty('isMain') && item.isMain;
                if (!isMain) {
                    return null;
                }
                let isEmail = ValidatorHelper.emailIsValid(item.url);
                let url = item.url;
                if (isEmail) {
                    url = "mailto:" + url;
                }
                return (
                    <li key={key} style={{listStyleType: 'none'}}>

                        {item.icon ? (
                            <>
                                <i className={item.icon}/>
                                &nbsp;
                            </>
                        ) : null}

                        {url ? (
                            <a href={url} target="_blank" rel="noreferrer">
                                {item.label ? item.label : item.url}
                            </a>
                        ) : (
                            <span>
                                {item.label}
                            </span>
                        )}
                    </li>
                );
            })}
        </ul>
    );
};
