import React from 'react';
import StringHelper from "../../../../../../../packages/ext/string/helpers/StringHelper";

function dateDiffInDays(a, b) {
    const _MS_PER_DAY = 1000 * 60 * 60 * 24;
    // Discard the time and time-zone information.
    const utc1 = Date.UTC(a.getFullYear(), a.getMonth(), a.getDate());
    const utc2 = Date.UTC(b.getFullYear(), b.getMonth(), b.getDate());

    return Math.floor((utc2 - utc1) / _MS_PER_DAY);
}

function generateWorkDaysAgo(diffDate) {
    let diffYear = Math.floor(diffDate / 365);
    let diffYearRest = diffDate % 365;
    let diffMonth = Math.floor(diffYearRest / 30);

    let yearMark = StringHelper.declOfNum(diffYear, ["год", "года", "лет"]);
    let monthMark = StringHelper.declOfNum(diffMonth, ["месяц", "месяца", "месяцев"]);

    let workDaysString = diffYear ? (diffYear + " " + yearMark) : "";
    if (diffMonth) {
        workDaysString = workDaysString + " " + diffMonth + " " + monthMark;
    }
    return workDaysString.trim();
}

function prepareItem(item) {
    let startDate = new Date(item.dateStart.year, item.dateStart.month - 1, 1);
    let endDate = new Date(item.dateEnd.year, item.dateEnd.month - 1, 1);
    item['workDays'] = dateDiffInDays(startDate, endDate) + 30;
    item['date'] = {
        start: new Date(item.dateStart.year, item.dateStart.month - 1, 1),
        end: new Date(item.dateEnd.year, item.dateEnd.month - 1, 1),
    };
}

function calculateWorkDays(companyHistory) {
    let workDaysAssocList = {};
    let workDaysTotal = 0;
    for (let key in companyHistory) {
        if (companyHistory.hasOwnProperty(key)) {
            let item = companyHistory[key];
            prepareItem(item);
            if (!workDaysAssocList.hasOwnProperty(item.position)) {
                workDaysAssocList[item.position] = 0;
            }
            workDaysAssocList[item.position] = workDaysAssocList[item.position] + item['workDays'];
            workDaysTotal += item['workDays'];
        }
    }

    let workDays = [];
    for (let position in workDaysAssocList) {
        if (workDaysAssocList.hasOwnProperty(position)) {
            let days = workDaysAssocList[position];
            workDays.push({
                position: position,
                days: days,
            });
        }
    }
    return {
        workDays,
        workDaysTotal
    };
}

export default function CompanyTab(props) {
    let monthNames = [
        "Январь", "Февраль", "Март", "Апрель", "Май", "Июнь",
        "Июль", "Август", "Сентябрь", "Октябрь", "Ноябрь", "Декабрь"
    ];

    let companyHistory = structuredClone(props['companyHistory']);
    companyHistory.reverse();

    let {workDays, workDaysTotal} = calculateWorkDays(companyHistory);

    return (
        <>

            <div className="card mt-3">
                <div className="card-body">
                    <ul>
                        {workDays.map(function (item, key) {
                            return (
                                <li key={key}>
                                    {item.position}:
                                    &nbsp;
                                    {/*<span className="badge badge-primary badge-pill">*/}
                                        {generateWorkDaysAgo(item.days)}
                                    {/*</span>*/}
                                </li>
                            );
                        })}
                        <li>
                            <b>
                                Общий стаж:
                            &nbsp;
                            {/*<span className="badge badge-primary badge-pill">*/}
                                {generateWorkDaysAgo(workDaysTotal)}
                            {/*</span>*/}
                            </b>
                        </li>
                    </ul>
                </div>
            </div>

            {companyHistory.map(function (item, key) {
                let workDaysString = generateWorkDaysAgo(item['workDays']);
                let startAt = monthNames[item.date.start.getMonth()] + " " + item.date.start.getFullYear();
                let endAt = monthNames[item.date.end.getMonth()] + " " + item.date.end.getFullYear();
                return (
                    <div key={key} className="card mt-3">
                        <div className="card-body">
                            <h5 className="card-title">{item.title}</h5>
                            {/*<h6 className="card-subtitle mb-2 text-muted">Card subtitle</h6>*/}
                            <p className="card-text">
                                {item.location}
                                {item.isRemote ? (
                                    <>
                                        &nbsp;
                                        <small className="badge111 badge-primary111">
                                            (удаленная работа)
                                        </small>
                                    </>
                                ) : null}
                                {item.website ? (
                                    <>
                                        &nbsp;|&nbsp;
                                        <a
                                            href={item.website}
                                            target="_blank"
                                            rel="noreferrer">
                                            {item.website}
                                        </a>
                                    </>
                                ) : null}
                            </p>

                            <p className="card-text">
                                {startAt} - {endAt} ({workDaysString})
                            </p>

                            <p className="card-text">
                                {item.position ? (
                                    <>
                                        <b>{item.position}</b>
                                    </>
                                ) : null}
                            </p>
                            <p className="card-text">
                                {item.description}
                            </p>
                            {/*<a href="#" className="card-link">Card link</a>*/}
                            {/*<a href="#" className="card-link">Another link</a>*/}
                        </div>
                    </div>
                );
            })}
        </>
    );
};
