import React, {Component} from 'react';
import CompanyTab from "../views/tabs/companyTab";
import pageHead from "../../../../../../packages/ext/pageTitle/singletons/pageTitle";
import container from "../../../../../../packages/core/container/singletons/container";
import CompanyServiceInterface from "../../../domain/services/CompanyServiceInterface";

export default class CompanyComponent extends Component {

    private title = 'Опыт работы';

    private service: CompanyServiceInterface;

    state = {
        collection: null,
    };

    constructor(props) {
        super(props);
        this.service = container.get('resume.services.company');
    }

    componentDidMount() {
        let collection = this.service.findAll();
        collection.then((collection) => {
            this.setState({collection});
        });
    }

    render() {
        let collection = this.state.collection;

        if (collection === null) {
            return null;
        }

        pageHead.setPageTitle(this.title);
        return (
            <>
                <h3 className="fw-bold">
                    {this.title}
                </h3>
                <CompanyTab companyHistory={collection}/>
            </>
        );
    }
}
