import React, {Component} from 'react';
import ProjectTab from "../views/tabs/projectTab";
import pageHead from "../../../../../../packages/ext/pageTitle/singletons/pageTitle";
import container from "../../../../../../packages/core/container/singletons/container";
import ProjectServiceInterface from "../../../domain/services/ProjectServiceInterface";

export default class ProjectsComponent extends Component {

    private title = 'Проекты';

    private service: ProjectServiceInterface;

    state = {
        collection: null,
    };

    constructor(props) {
        super(props);
        this.service = container.get('resume.services.project');
    }

    componentDidMount() {
        let collection = this.service.findAll();
        collection.then((collection) => {
            this.setState({collection});
        });
    }

    render() {
        let collection = this.state.collection;

        if (collection === null) {
            return null;
        }

        pageHead.setPageTitle(this.title);
        return (
            <>
                <h3 className="fw-bold">
                    {this.title}
                </h3>
                <ProjectTab projects={collection}/>
            </>
        );
    }
}
