import React from 'react';
import {Col, Container, Row} from "react-bootstrap";

import '../../../../packages/bundles/bootstrap4/style/bootstrap.min.css';
import '../../../bundles/sneat/dist/css/core.css';
import '../../../bundles/sneat/dist/css/theme-default.css';
