import configManager from "../../../../packages/core/configManager/singletons/configManager";
import Navigator from "../components/Navigator";
import Footer from "../components/Footer";

export default function AppLayout(props) {
    let copyrightConfig = {
        title: 'aboutData.shortName', // aboutData.shortName,
        text: '',
    };
    const appTitle = configManager.get('appTitle');

    if(copyrightConfig.text === null) {
        copyrightConfig.text = 'version ' + configManager.get('appVersion');
    }

    return (
        <>
            {/*<BeforeRenderWidget/>*/}
            <Navigator fixed="top" brand={appTitle}>
                {props.navigatorItems}
            </Navigator>
            {/*<Container className="mt-3">
                <Row>
                    <Col>
                        <BreadcrumbsWidget/>
                    </Col>
                </Row>
            </Container>*/}
            <Container as="main" role="main">
                {props.children}
            </Container>
            <Container>
                <Row>
                    <Col>
                        <Footer config={copyrightConfig}/>
                    </Col>
                </Row>
            </Container>
        </>
    );
}
