import React from 'react';

export default function ContactTab(props) {
    return (
        <>
            <div className="card mt-3">
                <div className="card-body">
                    <table className="table table-bordered table-sm" style={{width: "auto"}}>
                        <tbody>
                        {props.contacts.map(function (item, key) {
                            return (
                                <tr key={key}>
                                    <th>{item.type}</th>
                                    <td>
                                        {item.icon ? (
                                            <>
                                                <i className={item.icon}/>
                                                &nbsp;
                                            </>
                                        ) : null}
                                        {item.url ? (
                                            <a href={item.url} target="_blank" rel="noreferrer">
                                                {item.label || item.url}
                                            </a>
                                        ) : (
                                            <span>
                                        {item.label}
                                    </span>
                                        )}
                                    </td>
                                </tr>
                            );
                        })}
                        </tbody>
                    </table>
                </div>
            </div>
        </>
    );
};
