import CompanyServiceInterface from "../../../domain/services/CompanyServiceInterface";
import BaseRestApiAdapter from "./BaseRestApiAdapter";

export default class CompanyRestApiAdapter extends BaseRestApiAdapter implements CompanyServiceInterface {

    async findAll() {
        return new Promise((resolve, reject) => {
            let uri = this.apiEndpoint('companies');
            this.axiosInstance.get(uri)
                .then((result) => {
                    resolve(result.data);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
