import AboutServiceInterface from "../../../domain/services/AboutServiceInterface";
import BaseRestApiAdapter from "./BaseRestApiAdapter";

export default class AboutRestApiAdapter extends BaseRestApiAdapter implements AboutServiceInterface {

    async findOne() {
        return new Promise((resolve, reject) => {
            let uri = this.apiEndpoint('about');
            this.axiosInstance.get(uri)
                .then((result) => {
                    let entity = result.data;
                    let uri = this.apiEndpoint('contacts');
                    this.axiosInstance.get(uri)
                        .then((result) => {
                            entity.contacts = result.data;
                            resolve(entity);
                        })
                        .catch((e) => {
                            reject(e);
                        });
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
