import React, {Component} from 'react';
import ContactTab from "../views/tabs/contactTab";
import pageHead from "../../../../../../packages/ext/pageTitle/singletons/pageTitle";
import container from "../../../../../../packages/core/container/singletons/container";
import ContactServiceInterface from "../../../domain/services/ContactServiceInterface";

export default class ContactsComponent extends Component {

    private title = 'Мои контакты';

    private service: ContactServiceInterface;

    state = {
        collection: null,
    };

    constructor(props) {
        super(props);
        this.service = container.get('resume.services.contact');
    }

    componentDidMount() {
        let collection = this.service.findAll();
        collection.then((collection) => {
            this.setState({collection});
        });
    }

    render() {
        let collection = this.state.collection;

        if (collection === null) {
            return null;
        }

        pageHead.setPageTitle(this.title);
        return (
            <>
                <h3 className="fw-bold">
                    {this.title}
                </h3>
                <ContactTab contacts={collection}/>
            </>
        );
    }
}
