import React, {Component} from 'react';
import pageHead from "../../../../../../packages/ext/pageTitle/singletons/pageTitle";
import {Link} from "react-router-dom";
import container from "../../../../../../packages/core/container/singletons/container";
import HomeServiceInterface from "../../../domain/services/HomeServiceInterface";

export default class HomeComponent extends Component {

    private title = 'Главная страница';

    private service: HomeServiceInterface;

    state = {
        entity: null,
    };

    constructor(props) {
        super(props);
        this.service = container.get('resume.services.home');
    }

    componentDidMount() {
        let entity = this.service.findOne();
        entity.then((entity) => {
            this.setState({entity});
        });
    }

    render() {
        let homeData = this.state.entity;

        if (homeData === null) {
            return null;
        }



        pageHead.setPageTitle(this.title);
        return (
            <div className="centered-container">
                <h1>
                    {homeData.head}
                </h1>
                <p>
                    {homeData.text}
                </p>
                <p>
                    <Link to="/about" className="btn btn-primary">
                        {homeData.linkTitle}
                    </Link>
                </p>
            </div>
        );
    }
}
