import ContactServiceInterface from "../../../domain/services/ContactServiceInterface";
import BaseRestApiAdapter from "./BaseRestApiAdapter";

export default class ContactRestApiAdapter extends BaseRestApiAdapter implements ContactServiceInterface {

    async findAll() {
        return new Promise((resolve, reject) => {
            let uri = this.apiEndpoint('about');
            this.axiosInstance.get(uri)
                .then((result) => {
                    resolve(result.data);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }
}
