import React from 'react';
import Routes from "./config/routes";
import AppRouter from "../../packages/ext/app/router";
// import FloatContainers from "../../common/app/web/components/FloatContainers";
import WebLayout from "../app/web/layouts/layout";
import NavigatorItems from "../app/web/views/navigatorItems";
import {Route, Switch} from "react-router";
import HomeComponent from "../bundles/resume/presentation/react/components/homeComponent";
import FloatContainers from "./web/components/FloatContainers";

export default function IncloudApp(props) {
    return (
        <>
            <AppRouter>
                <Switch>
                    <Route path="/:name">
                        <WebLayout navigatorItems={<NavigatorItems/>}>
                            {Routes}
                        </WebLayout>
                    </Route>
                    <Route path="/">
                        <Route exact path="/" component={HomeComponent}/>
                    </Route>
                </Switch>
            </AppRouter>

            <FloatContainers/>
        </>
    );
}
